.maincontainer{
  display: flex;
  flex-wrap: wrap;
margin-top: 2rem;
  
  justify-content: space-evenly ;
  align-items: flex-start;
  width: 100vw;
 min-height: 60vh;
  padding: 0px;

}


.adhead{font-size: 1.5rem;
 display: flex;
 flex-wrap: wrap;
 justify-content: space-evenly;
 align-content: center;
  color: white;
  font-weight: bold;
  text-align: center;
  padding: 1rem;
 
  margin-top: 10px;
  width: 100vw;
  background-color: var(--rblue);

  
  align-items: center;

 
  }
  .pcategory{
    color: white;
  }
  .category{
    color: white;
  }



.pimage{
  /* box-shadow: 59px 45px 59px -34px rgba(0,0,0,0.75); */
 
border: 2px solid var(--rblue);
display: flex;

align-items: center;
justify-content: center;
padding: 2rem ;
margin: 1rem;
/* border: 1px solid; */
/* align-items: center; */

 /* height: 70vh; */
  width: 60vw;
  

 
  color: black;
}

.swiper {
  width: 100%;
 
  padding:0px;

}

.swiper-slide {
  background-position: center;
  background-size: cover;
 width: 100%;
 height: 100%;
 

}

.swiper-slide img {
  display: block;
  width: 100%;
  height: auto ;
}
.pcont{
  width: 36vw;
  background-color: var(--rblue);
color: black;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 1rem;
 margin-top: 2.5rem;
  padding: 2rem ;
}
.namecon{color:black;
  background-color: var(--main-color);
  width: 100%;
  display: flex;
  font-weight: 100;
  font-size: 1.5rem;
  gap: 1rem;
  padding:3rem 0;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  
  border: 2px solid black;
}

.pdis{margin-top: 2rem;
  margin-bottom: 4rem;
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 1rem ;
  text-align: center;
border: 1px solid;
color: black;
background-color: var(--rblue);
}
.pdetail{
  display: flex;
  justify-content: space-evenly;
  background-color: var(--rblue);
  align-items: center;
  width: 100vw;
  flex-wrap: wrap;
  padding: 4rem 0;
  margin-top: 2rem;    

}
.pdis h2{
  width: 50vw;
  font-size: 2.3rem;
  font-weight: 400;
  
  color: black;
}
.pcont h1{
  color: white;
}
.pdis h3{
  width: 50vw;
  font-size: 1.6rem;
  font-weight: 300;
  text-align: left;
 
}
.pconta{
  margin: 0;
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;

}


@media screen and (max-width:800px) {
  .pimage{
    width: 90vw;
    
  }
  .pdis{
    padding: 1rem;
  }
  .pcont{
    width: 90vw;
    margin-top: 2rem;
  }
  .adhead{
    width: 100vw;
  }
  
}
