.aboutco{

  min-height: 60vh;
  width: 100vw;
   
    margin: 4rem 0;
   
   
   
}
.heading{
    text-shadow: 10px 4px 9px rgba(0,0,0,0.6);
    margin: 0 2rem;
    font-size: 2rem;
    font-weight: 200;
    text-align: center;
 
    padding: 4rem 0 6rem 0;
    background-color: var(--rblue);
    clip-path: polygon(20% 0%, 80% 0%, 100% 0, 100% 82%, 47% 100%, 48% 100%, 0 81%, 0 0);
}
.content{
    box-shadow: 22px 26px 29px 2px rgba(0,0,0,0.75);
    margin: 2rem 20rem;
    background-color: var(--rblue);
    text-align: left;
    padding: 5rem 7rem;
    color: black;
    
    border-radius: 10px;
    
}
.contenth1{
    
    font-size: 3rem;
    font-weight: 400;
    padding-bottom: 3rem;
    

}
.content p{
    font-size: 1.7rem;
    padding-bottom: 3rem;


}
.btn1con{
    text-align: center;
}
.content .btn1{
    padding: 1rem;
    border: none;
    font-size: 2rem;
    border-radius: 10px;
    background-color: var(--main-color);
    color: black;

}
.content .btn1:hover{
    color: var(--rblue)

}

@media screen and (max-width:810px) {

.heading{
 margin: 0;
 font-size: 2.5rem
}
.contenth1{
    font-size: 4rem
    
}
.content p{
    font-size: 2.3rem;
    padding-bottom: 3rem;


}
.content{
    padding:4rem 2rem ;
}


}
@media screen and (max-width:600px) {

.heading{
 padding:6rem 0 ;
 font-size: 2rem
}
.content{
    padding:4rem 1rem ;
    margin:4rem 2rem ;
    font-size: 1rem;
}
.aboutco{

  
   
     
      margin: 4rem 0;
      
     
     
     
  }



}
   