.fcontainer{
    position: relative;
    width: 100vw;
    /* border-bottom: 2px solid royalblue; */
    padding-bottom: 2rem;
    
display: flex;
justify-content: center;
align-items: center;
gap: 1rem;
z-index: 4;



}
.location, .category{
    width: 25%;
    position: relative;  ;
    margin-top: 3rem;
    color: black;
    font-weight: 500;
    border: 1px solid var(--rblue);

}

.search{  border: 1px solid var(--rblue);
    position: relative;
    width: 35%;
    /* margin: 0 2rem; */
    margin-top: 1rem;
}
.sbtn {outline: none;
    border: none;
    background: none;
    color: var(--main-color);
    position: absolute;
    top: .6rem;
    right: 0.2rem;
    font-size: 3rem;
  
    
}
.searchb{
    transition: color .2s ease-in-out;
    font-weight: 200;
    color: var(--rblue);
}
.searchb:hover{
    color: black;
}
.catm{
    color: black;
}
.catm,input, textarea{
    width: 100%;
    border: none;
    
    font-size: 2rem;
    font-weight: 300;
    padding: 1rem .3rem;
    text-align: center;
    box-shadow: 20px 20px 20px -12px rgba(0,0,0,0.75);
    transition: all .3s ease-in-out;
    border: 1px solid var(--main-color);
    outline: none;
   

}
.maincategory{
    color:var(--rblue);
    font-size: 1.3rem;
    cursor: pointer;
   
}
option{
    color: red;
}.catm:hover,input:hover {
    box-shadow: none;
   /* border-radius: 10px; */
}
.maincat{
    width: 90vw;
    height: 100vh;
   background-color: var(--main-color);
    position: absolute;
    left: 5vw;
    top: 15rem;
    z-index: 6;
    border: 1px solid var(--rblue);
    box-shadow: 15px 26px 47px -6px rgba(18,18,16,0.75);
    display: flex;
    justify-content: flex-start;
    align-items: center;
    align-content: center;
    flex-direction: column;
    flex-wrap: wrap;
    gap: 1rem;
    padding: 2rem 1rem;
    /* background-color: blueviolet; */
   
 
}
.loc{ width: 70vw;
   height: 50vh;
background-color: var(--main-color);
    position: absolute;
    left: 5vw;
    top: 15rem;
    z-index: 6;
    border: 1px solid var(--rblue);
    box-shadow: 15px 26px 47px -6px rgba(18,18,16,0.75);
    padding: 2rem 1rem;
    color: black;

}
.loc h1{
    font-size: 3rem;
    width: 100%;
    text-align: center;
}
.loc .locdiv{
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: flex-start;
  
    flex-wrap: wrap;
    gap: 1rem;
    padding: 1rem;
    /* background-color: blueviolet; */
   
 
}
.loc .locdiv select{
    padding: .7rem 1rem;
    border: none;
    outline: none;
    border: 2px solid var(--rblue);
    box-shadow: 20px 20px 20px -12px rgba(0,0,0,0.75);
    transition: all .3s ease-in-out;
    border: 1px solid var(--main-color);
    outline: none;
    
    width: 20vw;

}
.loc .locdiv select option{
font-size: 1.2rem;
    color: black;
   border:1px solid yellow;
   box-shadow: 30px 20px 20px -12px rgba(0,0,0,0.75);
    text-align:left;


   

}
.catitem{
  color: black;
  font-size: 1.2rem;
  width: 17vw;
  cursor: pointer;
  /* background-color: aqua; */
  transition: all 0.3s ease-in-out;
}
  .catitem:hover{
  color: var(--rblue);
    
}


.paginationBox {
    display: flex;
    justify-content: center;
    margin: 6vmax;
  }
  
  .pagination {
    display: flex;
    justify-content: center;
    padding: 0;
  }
  
  .page-item {box-sizing: content-box;
      font-size: 1rem;
      font-weight: bolder;
    background-color: rgb(255, 255, 255);
    list-style: none;
    border: 1px solid rgba(0, 0, 0, 0.178);
    padding: 1vmax 1.5vmax;
    transition: all 0.3s;
    cursor: pointer;
  }
  .page-item:first-child {
    border-radius: 5px 0 0 5px;
  }
  
  .page-item:last-child {
    border-radius: 0 5px 5px 0;
  }
  .page-link {
    text-decoration: none;
    font: 300 2vmax "Roboto";
    color: rgb(80, 80, 80);
    transition: all 0.3s;
  }
  
  .page-item:hover {
    background-color: var(--rblue);
  }
  
  .page-item:hover .page-link {
    color: rgb(0, 0, 0);
  }
  
  .pageItemActive {
    background-color: var(--main-color);
  }
  
  .pageLinkActive {
    color: black;
  }
  

  @media screen and (max-width:1000px) {
    .catitem{
        width: 15vw;
    }
    .maincategory{
        width: 15vw;
    }
    .loc .locdiv select{
        width: 30vw;
    }
  }



@media screen and (max-width:810px) {
    .search , .location , .category{
        width: 33%;
       
    }
    .search{
        margin: 1rem 0.5rem;
    }
    .fcontainer{
        padding: 0 1rem;
        padding-bottom: 1rem;
      
    }
   
    select,input{
       font-size: 2rem;
    }
    :root{
        font-size: 50%;
    }
    .loc{
        width: 90vw;
        
    }

}
@media screen and (max-width:550px) {
.fcontainer{
    flex-direction: column;
    width: 100vw;
    gap: 2rem;
 
    margin-top: 1rem;
}
.fcontainer *{
    margin: .1rem;
}
.search , .location , .category{
    width: 90%;
   
}
.loc{
   align-items: flex-start;
   justify-content:flex-start;
   align-items: flex-start;
   gap: 2rem;
    
}
.loc .locdiv select{
    width: 80%;
}



}