header {
  position: relative;
  top: 0;
  left: 0;
  display: flex;
  justify-content: space-around;
  align-items: center;
  background-color: var(--bg);
  height: 6rem;
  perspective: 1000px;
  transform-style: preserve-3d;
  border-bottom: 3px solid var(--rblue);
  z-index: 13;
}
.logo {
  position: relative;
  top: -1.6rem;
  width: 12rem;
  padding-top: 2rem;
  border-bottom-left-radius: 100px;
  border-bottom-right-radius: 100px;
}
.logo img {
  object-fit: cover;
  position: absolute;
  top: 0rem;
  width: 100%;
  background-size: cover;
}
.item:hover,
.user:hover {
  color: var(--main-color);
  text-decoration: underline;
}
.navcontainer {
  display: flex;
}
.item {
  font-weight: 300;
  margin: 0 3rem;
  font-size: 2rem;
  color: white;
  transition: all 0.2s linear;
  font-family: "Roboto", sans-serif;
}
.user {
  color: white;
  font-size: 3rem;
  
  transition: all 0.2s linear;
}
.active .user {
  color: var(--main-color);
  font-weight: 500;
}
.bar {
  display: none;
}
.logincontainer {
  display: flex;
 
}
.active .item {
  color: var(--main-color);
  font-weight: 500;
}

/* /////////////////////media query////////////////////  */

@media screen and (max-width: 810px) {
  .navcontainer {
    flex-direction: column;
    position: absolute;
    right: 0;
    padding-top: 2rem;
    transform-origin: top;
    transform: rotateX(-90deg);
    top: 6rem;
    background-color: var(--rblue);
    height: 160px;
    justify-content: space-around;
    align-items: center;
    width: 100%;
    transition: transform 0.5s linear;
  }
  .bar {
    display: block;
    margin: 0 3rem;
  }
  header {
    justify-content: space-between;
    padding: 0 5rem;
  }
  .navactive {
    transform: rotateX(0deg);
  }
}

@media screen and (max-width: 900px) {
  .item {
    margin: 0 2rem;
  }
}
