.footer{
    position:relative;
    
 border-top: 10px solid black;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    flex-direction: column;
    background-color:var(--bg);
}
.navicons{
    width: 10%;
    display: flex;
    justify-content: space-evenly;

}
.fitem {
    font-size: 3rem;
    transition: .3s linear;
    margin: 1.2rem 0;
    /* margin-top: 2rem; */
}
.fitem:hover{
    color:var(--main-color)
}
.recent{
font-size: 3rem;
font-weight: 300;
/* text-decoration: underline; */
color: var(--white);
color: white;
transition: .3s linear;
margin-bottom: 1rem;
margin-top: 2rem ;
}
.recent:hover , .links a:hover{
    color:black
}
.links{
    width: 70vw;
    display: flex;
    justify-content: space-evenly;
    margin: 1rem 0;
    color: white;

}
.links a{
    text-decoration: none;
    font-size: 2rem;
    color: white;

}
.mainfooter{font-size: 1.7rem;
    padding: 2rem 5rem;
    /* background-color: var(--bgcolor); */
}
.mainfooter span{
    color: white;
    text-decoration: underline;
}

/* media query */
@media screen and (max-width:810px) {
:root{
    font-size: 50%;
}
.navicons ,.links{
    width: 100%;
    flex-wrap: wrap;
}


}
