.aboutco{
  

  
    width: 100vw;
     
      margin: 4rem 0;

  }
  
  .heading{
      text-shadow: 10px 4px 9px rgba(0,0,0,0.6);
      margin: 0 4rem;
      font-size: 3rem;
      font-weight: 200;
      text-align: center;
   
      padding: 4rem 0 8rem 0;
      background-color: var(--rblue);
      clip-path: polygon(20% 0%, 80% 0%, 100% 0, 100% 82%, 47% 100%, 48% 100%, 0 81%, 0 0);
  }
  .container{
      margin: 3rem 0;
      width: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
  }
  .login-box {
    

  
    width: 400px;
    padding:30px 60px;
    
    background: var(--rblue);
    box-sizing: border-box;
    box-shadow: 0 15px 25px rgba(0,0,0,.6);
    border-radius: 0;
  }
  .navicons{
    width: 50%;
    display: flex;
    justify-content: space-evenly;

}
  
  .login-box h2 {
    margin:  0 30px;
    padding-bottom: 2rem;
    color: #fff;
    text-align: center;
  }
  
  .login-box .user-box {
    position: relative;
  }
  
  .login-box .user-box input ,.login-box .user-box textarea, option{
    width: 100%;
    padding: 10px 0;
    font-size: 16px;
    color: black;
    
    margin: 2rem 0;
    border: none;
   
    outline: none;
    background: white;
  }
  .login-box .user-box label {
    position: absolute;
    top:-20px;
    left: 0;
    padding: 10px 0;
    font-size: 12px;
    color: black;
    pointer-events: none;
    transition: .5s;
  }
  
  .login-box .user-box input:focus ~ label,
  .login-box .user-box input:valid ~ label ,.login-box .user-box textarea:focus ~ label,.login-box .user-box textarea:valid ~ label  {
    top: -20px;
    left: 0;
    color:black;
    font-size: 12px;
  }
  
  .login-box form a ,.login-box form button {
    position: relative;
    display: inline-block;
    padding: 10px 20px;
    color: #03e9f4;
    font-size: 16px;
    text-decoration: none;
    text-transform: uppercase;
    overflow: hidden;
    transition: .5s;
    margin-top: 30px;
    letter-spacing: 4px
  }
  
  .login-box a:hover , .login-box button:hover {
    background: #03e9f4;
    color: #fff;
    border-radius: 5px;
    box-shadow: 0 0 5px #03e9f4,
                0 0 25px #03e9f4,
                0 0 50px #03e9f4,
                0 0 100px #03e9f4;
  }
  
  .login-box a span , .login-box button span {
    position: absolute;
    display: block;
  }
  
  .login-box a span:nth-child(1), .login-box button span:nth-child(1) {
    top: 0;
    left: -100%;
    width: 100%;
    height: 2px;
    background: linear-gradient(90deg, transparent, #03e9f4);
    animation: btn-anim1 1s linear infinite;
  }
  
  @keyframes btn-anim1 {
    0% {
      left: -100%;
    }
    50%,100% {
      left: 100%;
    }
  }
  
  .login-box a span:nth-child(2) , .login-box button span:nth-child(2) {
    top: -100%;
    right: 0;
    width: 2px;
    height: 100%;
    background: linear-gradient(180deg, transparent, #03e9f4);
    animation: btn-anim2 1s linear infinite;
    animation-delay: .25s
  }
  
  @keyframes btn-anim2 {
    0% {
      top: -100%;
    }
    50%,100% {
      top: 100%;
    }
  }
  
  .login-box a span:nth-child(3) , .login-box button span:nth-child(3) {
    bottom: 0;
    right: -100%;
    width: 100%;
    height: 2px;
    background: linear-gradient(270deg, transparent, #03e9f4);
    animation: btn-anim3 1s linear infinite;
    animation-delay: .5s
  }
  
  @keyframes btn-anim3 {
    0% {
      right: -100%;
    }
    50%,100% {
      right: 100%;
    }
  }
  
  .login-box a span:nth-child(4) , .login-box button span:nth-child(4) {
    bottom: -100%;
    left: 0;
    width: 2px;
    height: 100%;
    background: linear-gradient(360deg, transparent, #03e9f4);
    animation: btn-anim4 1s linear infinite;
    animation-delay: .75s
  }
  
  @keyframes btn-anim4 {
    0% {
      bottom: -100%;
    }
    50%,100% {
      bottom: 100%;
    }
  }
  
  @media screen and (max-width:810px) {
  
  .heading{
   margin: 0;
   font-size: 2.5rem
  }

  
  }
  @media screen and (max-width:600px) {
  
  .heading{
   padding:6rem 0.3rem ;
   font-size: 2rem
  }
  .container{
    margin: 4rem 1rem;
  }
  .login-box {
    width: 90%;
    padding: 2rem;
    margin: 3rem;
  }
 
  
  
  }
  
  @media screen and (max-width:500px) {
  
  .heading{
   padding:6rem 0.3rem ;
   font-size: 2rem
  }
  .container{
    margin: 2rem 1rem;
    padding: 0;
  }
  .login-box{
    width: 100%;
 
  
  
  }
}