.productcont{
    margin: 2rem 0;
    position: relative;
    transition: all 0.5s;
    width: 23rem;
    
min-height: 30rem;
    box-shadow: 20px 20px 20px -12px rgba(0,0,0,0.75);
    transition: all .3s ease-in-out;
     border: 1px solid white;
    /* border-radius: 10px; */
    background-color:var(--rblue);
}
.productcont img{
   position: relative;
    object-fit:cover;
    width: 100%;
    height: 17rem;

  /* border-top-left-radius: 10px;
  border-top-right-radius: 10px; */
 /* height: auto; */
}
.productcont .productcontent{
  
   position: relative;
   display: flex;
   justify-content: center;
   align-items: center;
   flex-direction: column;
   gap: 1rem;
   font-weight: 400;
    padding: 1rem;
    


    color: white;
    font-size: 2.1rem;
   
}
.productcont .productcontent .name{
    font-size: 1.7rem;
    font-weight: 400;
    transition: all .3s ease-in-out;
    
   
}
.productcont .productcontent .name:hover{
    
    text-decoration: underline;

}
.productcont .productcontent .price{
    font-size: 2rem;
    font-weight: 400;
    color: red;
   
}
.productcont .productcontent .locationcon{
    font-size: 1.5rem;
   
}
.productcont:hover{
    transform: translateY(-1.3rem);
    box-shadow: none;
   
}