.logincont{
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: aqua;
    color: black;
}
.fcon{
    color: #000;
    position: relative;
    left: 110px;
    font-size: 1.5rem;
    margin-top: 2rem;
}
.fcon:hover{
    color: #fff;
    transition: all 0.5s ease-in-out;
}
.togglediv{
    width: 100%;
    height: 10vh;
    display: flex;
    font-size: 3rem;
    justify-content: center;
    align-items: center;
    /* background-color: aqua; */
    color: black;
    margin-top: 2rem;
}
.togglediv button {
    font-size: 2rem;
    padding: 1rem 1.3rem;
    margin-left: 2rem;
    border: none;
    color: black;
    outline: none;
    background-color: var(--rblue);
    transition: all .3s linear;
}
.togglediv button:hover{
    color: white;
}
@media  screen and (max-width: 500px) {
    .togglediv{
        font-size: 1rem;
    
    }
    .togglediv button{
        margin-left: 1rem;
        font-size: 1rem;

    }
}
    
